import {Component, Input, EventEmitter} from '@angular/core';

@Component({
  selector: 'name-select',
  providers: [],
  directives: [],
  pipes: [ ],
  styles: [require('./name-select.style.css')],
  template: require('./name-select.html'),
  events : ['switchRequest']
})

export class NameSelectComponent {
  name: string;
  switchRequest:any;

  constructor() {
    this.switchRequest = new EventEmitter();
  }

  ngOnInit() {

  }

  ngOnChanges(){

  }

  selectName(name){
    this.name = name;
    this.switchToQuest();
  }

  switchToQuest(){
    console.log('switching to quest...')
    this.switchRequest.emit();

  }



}



